.mobile-button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: relative;
}

.mobile-button__burger {
  fill: red;
  margin: 0 auto;
}

.mobile-button__close-mobile {
  width: 14px;
  height: 14px;
  fill: #fff;
  margin-bottom: 26px;
  margin-left: auto;
}

.mobile-button__close-mobile:hover, .mobile-button__close-mobile:focus {
  fill: #d41443;
}

.mobile-button__buy {
  width: 100%;
  height: 44px;
  color: #d41443;
  background-color: #fff;
  border-radius: 22px;
  justify-items: baseline;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.mobile-button__buy:hover, .mobile-button__buy:focus {
  background-color: #ecebeb;
}

.mobile-button__buy:after {
  content: "";
  width: 6px;
  height: 8px;
  -webkit-mask-image: url("buy-arrow.38e819aa.svg");
  background-color: currentColor;
  margin-top: 3px;
  margin-left: 15px;
  display: inline-block;
  -webkit-mask-image: url("buy-arrow.38e819aa.svg");
  mask-image: url("buy-arrow.38e819aa.svg");
  -webkit-mask-position: center;
  mask-position: center;
  -webkit-mask-size: contain;
  mask-size: contain;
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
}

.mobile-menu__container {
  width: 100%;
  max-width: 250px;
  height: calc(101vh + 50px);
  z-index: 999;
  pointer-events: none;
  margin-top: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  overflow: hidden;
}

@media screen and (min-width: 1200px) {
  .mobile-menu__container {
    display: none;
  }
}

.mobile-menu__container.is-open {
  pointer-events: all;
}

.mobile-menu__menu {
  width: 100%;
  height: calc(100% - 50px);
  background-color: #e17992;
  flex-direction: column;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  transform: translateY(-170vh);
}

.mobile-menu__menu.is-open {
  transform: translateY(0);
}

.mobile-menu__menu.is-open:before {
  content: "";
  width: 100%;
  height: 50px;
  -webkit-clip-path: polygon(0 0, 50% 0, 0 100%);
  clip-path: polygon(0 0, 50% 0, 0 100%);
  background-color: #e17992;
  position: absolute;
  bottom: -49px;
  left: 0;
}

.mobile-menu__menu.is-open:after {
  content: "";
  width: 100%;
  height: 50px;
  -webkit-clip-path: polygon(50% 0%, 100% 0, 100% 100%);
  clip-path: polygon(50% 0%, 100% 0, 100% 100%);
  background-color: #e17992;
  position: absolute;
  bottom: -49px;
  right: 0;
}

